import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';

import { PreferencesService } from '../../shared/services/preferences/preferences.service';
import { CertificationsService } from '../../shared/services/mocks/certifications/certifications.service';
import { CertificationDto } from '../../shared/interfaces/certifications/certification.dto';
import { ExternalRedirect } from '../../shared/utils/ExternalLink';
import { SolutionsDto } from './../../shared/interfaces/solutions/solutions.dto';
import { ServicesDto } from './../../shared/interfaces/services/services.dto';
import { ServicesService } from './../../shared/services/mocks/services/services.service';
import { ChecklistDto } from './../../shared/interfaces/checklist/checklist.dto';
import { SolutionsService } from './../../shared/services/mocks/solutions/solutions.service';
import { ChecklistService } from './../../shared/services/mocks/checklist/checklist.service';
import { ToastMessageService } from './../../shared/services/toast/toast-message.service';
import { WhyStandService } from './../../shared/services/mocks/why-stand/why-stand.service';
import { WhyStandDto } from './../../shared/interfaces/why-stand/why-stand.dto';
import { ToastEnum, ToastPrimeSeverityEnum } from './../../shared/constants/toast.constant';

@Component({
  selector: 'app-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage implements OnInit, OnDestroy {

  heroImg: string;
  services: ServicesDto[] = [];
  solutions: SolutionsDto[] = [];
  checklist: ChecklistDto[] = [];
  certifications: CertificationDto[] = [];
  whyStandList: WhyStandDto[] = [];

  private subscriptions = new Subscription();

  constructor(
    private readonly route: Router,
    private readonly preferencesService: PreferencesService,
    private readonly servicesService: ServicesService,
    private readonly solutionsService: SolutionsService,
    private readonly certificationsService: CertificationsService,
    private readonly checklistService: ChecklistService,
    private readonly whyStandService: WhyStandService,
    private readonly toastService: ToastMessageService,
  ) { }
  // Fazer o formulario e acertar os botoes para as proximas paginas
  ngOnInit() {
    const theme = this.preferencesService.getThemePreference()
    this.heroImg = `assets/imgs/logos/Stand FTW - Cover.png`;
    this.changeHeroImg();
    this.loadServices();
    this.loadSolutions();
    this.loadChecklist();
    this.loadWhyStand();
    this.loadCertifications();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadServices() {
    this.servicesService.list()
      .subscribe(data => {
        this.services = data;
      });
  }
  loadSolutions() {
    this.solutionsService.list()
      .subscribe(data => {
        this.solutions = data;
      });
  }
  loadChecklist() {
    this.checklistService.list()
      .subscribe(data => {
        this.checklist = data;
      });
  }

  loadCertifications() {
    this.certificationsService.list()
      .subscribe(data => {
        this.certifications = data;
      });
  }
  loadWhyStand() {
    this.whyStandService.list()
      .subscribe(data => {
        this.whyStandList = data;
        console.log(this.whyStandList);
      });
  }

  openLink(url: string) {
    ExternalRedirect.externalLinkHandle(url);
  }

  ionViewWillEnter() {

  }

  changeHeroImg() {
    this.preferencesService.themeSubscriber.subscribe(theme => {
      // this.heroImg = `assets/imgs/fotos/my-github-${theme}.png`;
    });
  }

  navigate(url: string) {
    this.route.navigateByUrl(url);
  }

  changeWhySide(index: number) {
    return !(index % 2 == 0);
  }

  knowMore(solution: SolutionsDto) {
    this.toastService.presentToast({
      detalhe: `Em breve`,
      titulo: `Aguarde`,
      duracao: ToastEnum.shortDuration,
      gravidade: ToastPrimeSeverityEnum.ATENCAO,
    });
  }

}